import { useAccordion } from "./useAccordion";
import { CustomAccordion } from "./CustomAccordion";
import { CardMediaPlayer } from "./CardMediaPlayer";
import { Layout } from "../Layout";
import { Grid } from "@mui/material";

const MediaPlayer = (props) => {

    const { entry} = props
    const { expanded, pause, handleChange } = useAccordion()

    const handleError = (e) => {
        console.log(e)
    }

    return (
        <Layout>
            <Grid container columns={14} sx={{mx: 5, my: 5}} spacing={3}>
                <Grid item xs={6}>
                    <CustomAccordion
                        title={`Card Media Player`}
                        expanded={expanded}
                        handleChange={handleChange}
                    >
                    <CardMediaPlayer 
                        pause={pause}
                        height={250}
                        width={"100%"}
                        url={entry.url}
                        onError={handleError}
                    />
                </CustomAccordion>
            </Grid>
        </Grid>
      </Layout>
    )

}



export default MediaPlayer;
