import CardMedia from '@mui/material/CardMedia';
import { Card } from '@mui/material';

export const CardMediaPlayer = ({pause, height, width, url, onError}) => {
  return (
        <Card sx={{display: "flex"}}>
          <CardMedia 
            component='video'
            image={url}
            autoplay
            controls
            muted={pause}
            sx={{objectFit: "contain", height: height, width: width}}
            onError={onError}
            />
    </Card>
  );
}

