
import { useState } from 'react';


export const useAccordion = () => {

    const [expanded, setExpanded] = useState(false)
    const [pause, setPause] = useState(true)
  
    const handleChange = (e) => {
      setExpanded(!expanded)
      setPause(expanded)
    }
  
    return {
        expanded, pause, handleChange
    }
}
